body {
  position: relative;
  width: 100%;
  height: 100%;
  display: inline-block;
  margin: 0px;
  float: left;
}

#root {
  display: block;
  margin: 0px auto;
  -webkit-user-select: none;
  user-select: none;
  position: relative;
  width: 100vh;
  max-width: 761px;
}

#boardContainer {
  width: 100%;
  padding-top: 100%;
  position: relative;
  display: inline-block;
  margin: 0px;
  float: left;
}

#board {
  border: 2px solid #0d0400;
  background-color: #0d0400;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
}

.boardRow {
  width: 100%;
  height: calc(100% / 3);
  display: flex;
}

.quadrant {
  border: 0px;
  width: calc(100% / 3);
  height: calc(100%);
  display: inline-block;
  float: left;
}

.quadrant.home,
.quadrant.start {
  border: 2px solid #0d0400;
  width: calc((100% / 3) - 4px);
  height: calc(100% - 4px);
}

.file-v {
  width: calc(100% / 3);
  height: 100%;
  display: inline-block;
  float: left;
}

.row-h {
  width: 100%;
  height: calc(100% / 3);
  display: inline-block;
  float: left;
}

.space {
  display: inline-block;
  float: left;
  background-color: #f1efee;
  border: 2px solid #0d0400;
  text-align: center;
  position: relative;
}

.file-v .space {
  width: calc(100% - 4px);
  height: calc((100% / 8) - 4px);
}

.row-h .space {
  width: calc((100% / 8) - 4px);
  height: calc(100% - 4px);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.row-h .token {
  margin: 1px 0px;
}

.highlightSpace {
  background-color: #f3e66d;
}

.highlightSpace.safe {
  background-color: #f0b520;
}

.ramp .highlightSpace.space.safe {
  background-color: #f3e66d;
}

.ramp .highlightSpace.space {
  background-color: #f3e66d;
}

.home .highlightSpace {
  background-color: #f3e66d;
}

.safe,
.ramp .space.safe {
  background-color: #cf2d2d;
  text-align: center;
  position: relative;
}

.safe::after {
  background-color: #FFF;
  margin: 0 auto;
  position: absolute;
  top: 50%;
  left: 50%;
  -ms-transform: translateY(-50%) translateX(-50%) rotate(45deg);
  transform: translateY(-50%) translateX(-50%) rotate(45deg);
  text-align: center;
  border-radius: 2px;
  content: ' ';
  border: 3px solid #0d0400;
}

.file-v .safe::after {
  width: calc((80% / 3) - 4px);
  height: calc(80% - 4px);
}

.row-h .safe::after {
  width: calc(80% - 4px);
  height: calc((80% / 3) - 4px);
}

.ramp .space {
  background-color: #2d5b3b;
}

.start {
  background-color: #f1efee;
}

.outerCircle {
  border: 4px solid #0d0400;
  border-radius: calc(100% - 8px);
  display: inline-block;
  float: left;
  width: calc(100% - 8px);
  height: calc(100% - 8px);
  position: relative;
}

.innerCircle {
  border: 4px solid #000000;
  border-radius: calc(70% - 4px);
  display: inline-block;
  float: left;
  width: calc(75% - 4px);
  height: calc(75% - 4px);
  background-color: #afd3cc;
  position: absolute;
  top: 50%;
  left: 50%;
  -ms-transform: translateY(-50%) translateX(-50%);
  transform: translateY(-50%) translateX(-50%);
  text-align: center;
}

.startName {
  display: inline-block;
  width: 100%;
  font-size: 2.35vh;
  font-family: Arial;
  font-weight: bold;
  line-height: 4.3vh;
  margin-top: 5%;
}

.home {
  background-color: #f1efee;
  position: relative;
}

.innerHome {
  display: inline-block;
  width: calc(90% - 4px);
  height: calc(90% - 4px);
  border: 2px solid #000;
  background-color: #afd3cc;
  margin: 5%;
  border-radius: 10%;
  position: relative;
}

.playerHome {
  position: absolute;
  z-index: 100;
  text-align: center;
}

.playerHomeTop {
  height: 25%;
  width: calc(100% / 3);
  top: 0px;
  left: calc(100% / 3);
}

.playerHomeLeft {
  width: 30%;
  height: calc(100% / 3);
  top: calc(100% / 3);
  left: 0px;
}

.playerHomeRight {
  width: 30%;
  height: calc(100% / 3);
  top: calc(100% / 3);
  right: 0px;
}

.playerHomeBottom {
  height: 25%;
  width: calc(100% / 3);
  bottom: 0px;
  left: calc(100% / 3);
}

.centerMenu {
  display: inline-block;
  width: 80%;
  height: 50%;
  position: absolute;
  top: 25%;
  left: 10%;
  text-align: center;
  border-radius: 8%;
}

.centerMenu.preMenu {
  top: 5%;
}

.playerTurn {
  display: inline-block;
  width: 100%;
  font-size: 2.35vh;
  font-family: Arial;
  font-weight: bold;
  line-height: 4.3vh;
}

.diceArea table {
  font-size: 1.3125vh;
  font-family: Arial;
}

.diceArea table td:nth-child(1) {
  font-weight: bold;
}

.btnArea {
  width: 100%;
  display: inline-block;
  float: left;
}

#rollBtn {
  display: inline-block;
  font-size: 2.1vh;
  padding: 5px 10px;
  background-color: #440d20;
  color: #FFF;
  font-family: Arial;
  cursor: pointer;
  border: 2px solid #0d0400;
  margin: 0 0 5px 0;
}

#rollBtn.disableBtn {
  background-color: #696969;
  cursor: not-allowed;
}

.startPosition {
  display: block;
  margin: 0 auto;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateY(-50%) translateX(-50%);
  text-align: center;
  height: 39%;
  width: 50%;
}

.token {
  margin: -2px 1px;
  border: 2px solid #0d0400;
  border-radius: 100%;
  display: inline-block;
  width: calc(100vh / 24 - 4px);
  height: calc(100vh / 24 - 4px);
  cursor: pointer;
  z-index: 1000;
  position: relative;
}

.player1Token {
  background-color: #651515;
}

.player1Start {
  background-color: #651515;
}

.player2Token {
  background-color: #155265;
}

.player2Start {
  background-color: #155265;
}

.player3Token {
  background-color: #0f4a1f;
}

.player3Start {
  background-color: #0f4a1f;
}

.player4Token {
  background-color: #606515;
}

.player4Start {
  background-color: #606515;
}

