.diceArea {
  display: inline-block;
}

.die {
  width: calc(100vh / 24);
  height: calc(100vh / 24);
  display: inline-block;
  background-color: #FFF;
  margin: 2px;
  border: 2px solid #0d0400;
  border-radius: 6px;
  color: #0d0400;
  line-height: 9px;
  font-size: 23px;
  position: relative;
  float: left;
}

.die.spentDie {
  background-color: #696969;
}

.die span {
  position: absolute;
  display: none;
}

.die-1 span:nth-child(1) {
  display: inline-block;
  top: 50%;
  left: 50%;
  transform: translateY(-50%) translateX(-50%);
}

.die-2 span:nth-child(1) {
  display: inline-block;
  top: 15%;
  right: 15%;
}

.die-2 span:nth-child(2) {
  display: inline-block;
  bottom: 15%;
  left: 15%;
}

.die-3 span:nth-child(1) {
  display: inline-block;
  top: 12%;
  right: 12%;
}

.die-3 span:nth-child(2) {
  display: inline-block;
  top: 50%;
  left: 50%;
  transform: translateY(-50%) translateX(-50%);
}

.die-3 span:nth-child(3) {
  display: inline-block;
  bottom: 12%;
  left: 12%;
}

.die-4 span:nth-child(1) {
  display: inline-block;
  top: 15%;
  left: 15%;
}

.die-4 span:nth-child(2) {
  display: inline-block;
  top: 15%;
  right: 15%;
}

.die-4 span:nth-child(3) {
  display: inline-block;
  bottom: 15%;
  left: 15%;
}

.die-4 span:nth-child(4) {
  display: inline-block;
  bottom: 15%;
  right: 15%;
}

.die-5 span:nth-child(1) {
  display: inline-block;
  top: 12%;
  right: 12%;
}

.die-5 span:nth-child(2) {
  display: inline-block;
  top: 12%;
  left: 12%;
}

.die-5 span:nth-child(3) {
  display: inline-block;
  top: 50%;
  left: 50%;
  transform: translateY(-50%) translateX(-50%);
}

.die-5 span:nth-child(4) {
  display: inline-block;
  bottom: 12%;
  right: 12%;
}

.die-5 span:nth-child(5) {
  display: inline-block;
  bottom: 12%;
  left: 12%;
}

.die-6 span:nth-child(1) {
  display: inline-block;
  top: 12%;
  left: 15%;
}

.die-6 span:nth-child(2) {
  display: inline-block;
  top: 50%;
  left: 15%;
  transform: translateY(-50%);
}

.die-6 span:nth-child(3) {
  display: inline-block;
  bottom: 12%;
  left: 15%;
}

.die-6 span:nth-child(4) {
  display: inline-block;
  top: 12%;
  right: 15%;
}

.die-6 span:nth-child(5) {
  display: inline-block;
  top: 50%;
  right: 15%;
  transform: translateY(-50%);
}

.die-6 span:nth-child(6) {
  display: inline-block;
  bottom: 12%;
  right: 15%;
}

.die b,
.die strong,
.die-10 span,
.die-20 span {
  display: none;
}

.die-10 b,
.die-20 strong {
  text-align: center;
  display: inline-block;
  line-height: 4.25vh;
  font-family: Arial;
  font-size: 2.75vh;
}

.diceArea table input:nth-child(1) {
  width: 26px;
  left: 0px;
}

.diceArea table input:nth-child(2) {
  width: 35px;
  left: 27px;
}

.diceArea table input:nth-child(3) {
  width: 34px;
  right: 0px;
}

.switch-toggle {
  float: left;
  background-color: #2d3035;
  border-radius: 3px;
  border: 1px solid #2d3035;
  position: relative;
}

.switch-toggle input {
  position: absolute;
  opacity: 0;
  margin: 0px;
  height: 100%;
}

.switch-toggle label {
  padding: 0.92vh;
  float: left;
  color: #fff;
  cursor: pointer;
  position: relative;
}

.switch-toggle input:checked+label {
  background: #478b46;
  /*box-shadow: 0 1px 1px rgb(0 0 0 / 20%), inset 0 1px 1px rgb(255 255 255 / 45%);
  background-image: -webkit-linear-gradient(top, rgba(255, 255, 255, 0.2), transparent);
  background-image: linear-gradient(to bottom, rgba(255, 255, 255, 0.2), transparent);*/
}

@media(min-height: 761px) {
  #rollBtn {
    font-size: 16px;
  }

  .diceArea table {
    font-size: 10px;
  }

  .startName,
  .playerTurn {
    font-size: 18px;
    line-height: 32px;
  }

  .switch-toggle label {
    padding: 7px;
  }

  .token {
    width: 28px;
    height: 28px;
  }

  .diceArea .die {
    width: 35px;
    height: 35px;
  }

  .die-10 b,
  .die-20 strong {
    line-height: 32px;
    font-size: 21px;
  }
}